import * as React from "react";
import Box from "@mui/material/Box";
import InputLabel from "@mui/material/InputLabel";
import MenuItem from "@mui/material/MenuItem";
import FormControl from "@mui/material/FormControl";
import Select from "@mui/material/Select";

export default function BasicSelect({ datumNumber, results, setResults }) {
  const handleChange = (event) => {
    const newResults = [...results];
    newResults[datumNumber] = event.target.value;
    setResults(newResults);
  };

  return (
    <Box sx={{ minWidth: 120 }}>
      <FormControl fullWidth>
        <InputLabel id="demo-simple-select-label">Classification</InputLabel>
        <Select
          labelId="demo-simple-select-label"
          id="demo-simple-select"
          value={results[datumNumber]}
          label="Classification"
          onChange={handleChange}
        >
          <MenuItem value={""}></MenuItem>
          <MenuItem value={"1"}>Add endpoint to composite endpoint</MenuItem>
          <MenuItem value={"2"}>Add endpoint</MenuItem>
          <MenuItem value={"3"}>Change endpoint</MenuItem>
          <MenuItem value={"4"}>Change endpoint, add endpoint</MenuItem>
          <MenuItem value={"5"}>
            Change in endpoint in a composite endpoint
          </MenuItem>
          <MenuItem value={"6"}>Change method of analysis</MenuItem>
          <MenuItem value={"7"}>Change number of events</MenuItem>
          <MenuItem value={"8"}>Change population</MenuItem>
          <MenuItem value={"9"}>Change readers</MenuItem>
          <MenuItem value={"10"}>Change timepoint</MenuItem>
          <MenuItem value={"11"}>Error</MenuItem>
          <MenuItem value={"12"}>From nothing to something</MenuItem>
          <MenuItem value={"13"}>Refine definition</MenuItem>
          <MenuItem value={"14"}>Remove endpoint</MenuItem>
          <MenuItem value={"15"}>Shuffle Endpoints</MenuItem>
          <MenuItem value={"16"}>Syntax</MenuItem>
        </Select>
      </FormControl>
    </Box>
  );
}
